
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin-top: 60px;
  font-size: 1.3em;
}

.editor > h1:empty::after, .new-paragraph { 
  color: #cecece;
  font-style: italic;
  margin-left: 10px;
}
.editor > h1:empty::after {
  content: 'Titulo';
}
.new-paragraph {
  content: 'Escreva Aqui';
}
.editor:focus {
  border: none;
  outline: none;
}
